
















































const io = require('../../utils/socket.io.js');
import {
	mapState,
	mapActions
} from "vuex";
export default {
	computed: {
		...mapState(["token"])
	},
	data() {
		return {			
			oIndex: 0,
			dkList: [],
			productList: [],
			ldata: false,
			showMap: false,
			latitude: 113.9304,
			longitude: 113.9304,
			width: 21,
			height: 29,
			iconPath: '/static/img/location.png',
			markers: [],				
			apply: {
				ORDER_PRODUCTTYPE: 1,
				Latitude: uni.getStorageSync('latitude'),
				Longitude: uni.getStorageSync('longitude')
			},
			socket: null,
			hasConnected: false
		}
	},
	methods: {
		findmanger(){
			var longitude = this.longitude;
			var latitude = this.latitude;
			var busnessType = this.oIndex + 1;
			if(this.socket){
				this.socket.emit('findmanger', { longitude, latitude, busnessType });
				clearInterval(this.timer);
				this.timer = null;
				this.timer = setInterval(()=>{
					this.socket.emit('findmanger', { longitude, latitude, busnessType });
				}, 5000);
			}
		},
		initSocket() {
			 const that= this;
			 const socket = io(
			  'http://rancher.rongxiaoxiao.com/'
			);	
			 this.socket  =  socket;			
			 socket.on('connect', () => {
				this.Util.Toast.toast('connect')
				this.changeTab(0);
				this.hasConnected = true;
			});
			
			if(this.token){
				socket.emit('oauth', {
					token: this.token
				});						
			}
			
			socket.on('oauthresult', (data) => {
				this.Util.Toast.toast('token认证成功');
			});
						
			socket.on('orderchange',  (data) => {
				this.Util.Toast.toast('您的订单已被接单');
			})
			
			// 添加一个关闭连接的监听器
			socket.on('disconnect',  () => {
				this.Util.Toast.toast("socket与服务器断开连接");
			});
			 // 查询附近的信贷经理返回
			socket.on('positionresult',  (data) => {
				if(data.length){
					that.markers = data.map((item,index)=>{
						return {
							id: item.userID,
							title: item.NAME,
							latitude: parseFloat(item.LATITUDE),
							longitude: parseFloat(item.LONGITUDE),
							width: 14,
							height: 22,
							iconPath: that.iconPath
					  }
					})
					
				}			
			});
	
			socket.on('connect_error', d => {
			  this.Util.Toast.toast("connect_error");
			})			
		},
		appoint() {
			if (!this.token) {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 1500,
					mask: true
				});
				setTimeout(()=>{
					uni.redirectTo({
						url: '/pages/login/login'
					})
				}, 2000)	
				return
			}			
			if(this.markers.length == 0){
				this.Util.Toast.toast("附近没有信贷经理");
				return;
			}
							
			this.Util.Toast.showModal("确认预约", "附近有" + this.markers.length + "个信贷经理，是否预约", res => {
				if (res.confirm) {
					this.API.createOrder(this.apply).then(res => {
						this.Util.Toast.toast("预约成功");
					});
				}
			});
		},
		goApply(title) {
			if (!this.token) {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 1500,
					mask: true
				});
				setTimeout(()=>{
					uni.redirectTo({
						url: '/pages/login/login'
					})
				}, 2000)	
				return
			}	
			uni.navigateTo({
				url: '/pages/user/userInfo/userInfo?title=' + title
			});
		},
		goProductShow(id) {
			uni.navigateTo({
				url: '/pages/product-show/product-show?ID=' + id
			});
		},
		changeTab(index) {
			this.oIndex = index;
			this.findmanger();
		},
		bindmarkertap() {
			//console.log('marker被点击');
		},
		bindtap() {
			//console.log('map被点击');
		},
		toCenter() {
			this.mapCtx.moveToLocation();
		},
		getLocation() {
			// 微信获得经纬度
			var that = this;
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					var latitude = res.latitude;
					var longitude = res.longitude;
					uni.setStorageSync('latitude', latitude);
					uni.setStorageSync('longitude', longitude);

					that.latitude = latitude;
					that.longitude = longitude;
					that.Util.Toast.toast("定位成功")
					
					!that.hasConnected  &&  that.initSocket();
				},
				fail: function(res) {
					that.Util.Toast.toast("获取定位失败");
					that.ldata = true;
				}
			})
		},
		
	},
	onUnload() {
		
	},
	onShow() {		
		this.mapCtx = uni.createMapContext('myMap');
		this.toCenter();
		this.getLocation();
					
		// 贷款类型
		this.API.getProductList({
			type: 2
		}).then(res => {
			this.dkList = res.data;
		});
		// 贷款产品
		this.API.getProductList({
			type: 3
		}).then(res => {
			this.productList = res.data;
		})
	}
}
